.button {
  border: 2px solid $color-button-article;
  border-radius: 8px;
  background: $color-button-article;
  display: flex;
  height: 56px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: $color-button-text;
  text-align: center;
  font-family: $font-family;
  font-size: $font-size-2;
  font-style: $font-style;
  font-weight: $font-weight-bold;
  line-height: 27px;
  transition-duration: 0.5s;
  margin-top: 16px;
  &--primary {
    height: 56px;
    width: 280px;
    margin-bottom: 24px;
    @include for-tablet() {
      margin-bottom: 32px;
      width: 600px;
    }
    @include for-desktop() {
      margin-bottom: 0;
      width: 498px;
    }
  }
  &:hover {
    background: $color-article;
    border: 2px solid $color-article;
  }
  &:active {
    background: $color-button-active;
    border: 2px solid $color-button-active;
  }
  &--container {
    display: grid;
    @include grid();
    &--secondary {
      box-sizing: border-box;
      height: 56px;
      width: 280px;
      margin-left: 20px;
      margin-bottom: 96px;
      margin-right: 20px;
      @include for-tablet() {
        width: 600px;
        // margin-left: 48px;
        margin-left: 72px;
        margin-bottom: 120px;
      }
      @include for-desktop() {
        width: 324px;
        margin-left: 120px;
        margin-bottom: 120px;
        grid-column: 1 / 4;
      }
    }
  }
}
